.graduation,
.puc,
.sslc {
    padding: 5%; /* Use percentage for padding */
    position: relative;
    height: 20%; /* Use percentage for height */
    width: 20%; /* Use percentage for width */
    border-radius: 3px;
    box-sizing: border-box; /* Include padding and border in the box sizing */
}

.graduation {
    left: 5%; /* Use percentage for left position */
    border: 2px solid aqua; /* Specify border style */
    background-color: rgba(95, 158, 160, 0.747);
}

.puc {
    top: -22%; /* Use negative percentage for top position */
    left: 25%; /* Use percentage for left position */
    border: 2px solid green; /* Specify border style */
    background-color: rgba(0, 128, 0, 0.678);
}

.sslc {
    top: -45%; /* Use negative percentage for top position */
    left: 50%; /* Use percentage for left position */
    border: 2px solid blueviolet; /* Specify border style */
    background-color: rgba(128, 0, 100, 0.555);
}

@media (max-width: 768px) {
    /* Adjust styles for screens with a maximum width of 768 pixels */
    .graduation,
    .puc,
    .sslc {
        width: 100%; /* Adjust width for smaller screens */
        left: 0; /* Reset left position */
        top: 0; /* Reset top position */
    }
}

