.skills {
  /* Your existing styles for the .skills container */

  /* Additional styles for the image container */
  .image {
      display: flex;
      flex-wrap: wrap; /* Allow images to wrap to the next line on smaller screens */
      justify-content: space-evenly; /* Adjust as needed */
      align-items: center; /* Adjust as needed */
  }

  /* Styles for each image */
  img {
      width: 100%; /* Set the width to 100% of the container */
      height: auto; /* Maintain aspect ratio */
      max-width: 200px; /* Set the maximum width for larger screens */
      margin: 10px; /* Adjust margin as needed */
      transition: transform 0.3s ease; /* Smooth transition for the hover effect */
  }

  img:hover {
      transform: scale(1.2);
  }
}

@media (max-width: 768px) {
  /* Adjust styles for screens with a maximum width of 768 pixels */
  .image {
      justify-content: center; /* Center images on smaller screens */
  }

  img {
      max-width: 100%; /* Set the maximum width to 100% for smaller screens */
  }
}

