.resume {
    max-width: 100%; /* Set maximum width to 100% for responsiveness */
    width: 80%; /* Set a relative width */
    margin: 20px auto; /* Center the element with auto margins */
}

.information {
    color: orange;
}

.News {
    position: relative; /* Change to relative positioning */
    top: 0; /* Reset top position */
    left: 0; /* Reset left position */
    margin-top: 20px; /* Add margin to create space between elements */
}

.para {
    color: orange;
}

.monkey {
    position: relative; /* Change to relative positioning */
    top: 0; /* Reset top position */
    right: 0; /* Reset right position */
    margin-top: 20px; /* Add margin to create space between elements */
}

@media (max-width: 768px) {
    /* Adjust styles for screens with a maximum width of 768 pixels */
    .News,
    .monkey {
        position: static; /* Reset position to normal flow */
        margin-top: 10px; /* Adjust margin for smaller screens */
    }
}

