.container {
  height: 50vh; /* 50% of the viewport height */
  background-color: color-mix(in srgb, rgba(0, 0, 255, 0.719) 50%, grey 50%);
  position: relative;
}

.photo {
  border-radius: 50%;
  width: 20vw; /* 20% of the viewport width */
  height: 20vw; /* 20% of the viewport width */
  position: absolute;
  top: 15%; /* Adjusted for better responsiveness */
  left: 5%; /* Adjusted to place the image on the left side of the screen */
}


.profile {
  position: absolute;
  top: 20%; /* Adjusted for better responsiveness */
  left: 50%; /* Adjusted for better responsiveness */
  color: orange;
  transform: translateX(-50%); /* Center horizontally */
}

.profile h3 {
  display: inline-block;
  position: relative;
}

.profile h3::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #3498db;
  bottom: 0;
  left: 0;
  transform-origin: bottom right;
  animation: cursorAnimation 1s infinite;
}

@keyframes cursorAnimation {
  0% {
      transform: scaleX(0);
  }
  50% {
      transform: scaleX(1);
  }
  100% {
      transform: scaleX(0);
  }
}

.container {
  background: linear-gradient(to right, #3498db, #e74c3c);
  background-size: 200% 200%;
  animation: gradientAnimation 5s infinite;
}

@keyframes gradientAnimation {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@media (max-width: 768px) {
  /* Adjust styles for screens with a maximum width of 768 pixels */
  .photo {
    width: 50%;
    height: 50%;
    left: 0;
    position: absolute; /* Add this line to position it absolutely */
}

  .profile {
      width: 80%; /* Adjust width for smaller screens */
      left: 10%; /* Adjust left position for smaller screens */
      transform: translateX(0); /* Reset horizontal centering for smaller screens */
  }
}

